import React, { useState } from 'react';
import Accordion from './components/Accordion';
import Search from './components/Search';
import DropDown from './components/DropDown';
import Translate from './components/Translate';
import Route from './components/Route';
import Header from './components/Header';

const items = [
    {
        title: 'What is React?',
        content: 'It is a popular Javascript based front-end framework'
    },
    {
        title: 'Why use React?',
        content: 'It is the favorite Javascript based front-end framework'
    },
    {
        title: 'How to use React?',
        content: 'Use it to build components'
    }
];

const options = [
    {
        label: 'Red',
        value: 'red'
    },
    {
        label: 'Green',
        value: 'green'
    },
    {
        label: 'Blue',
        value: 'blue'
    }
];

// const showComponent = (route, component) => {
//     return window.location.pathname === route ? component : null;
// };

export default () => {
    const [ selected, setSelected ] = useState(options[ 0 ]);
    // const [ showDropdown, setShowDropdown ] = useState(true);

    return (
        <div>
            <Header />
            <Route path='/'>
                <Accordion items={items} />
            </Route>
            <Route path='/list'>
                <Search />
            </Route>
            <Route path='/dropdown'>
                <DropDown
                    label='Pick a color'
                    selected={selected}
                    setSelected={setSelected}
                    options={options}
                />
                <h1 style={{ color: selected.value, textAlign: "center" }}>Hello There</h1>
            </Route>
            <Route path='/translate'>
                <Translate />
            </Route>
        </div >
    )
};