import React, { useState, useEffect } from 'react';
import axios from 'axios';

const Search = () => {
    const [ term, setTerm ] = useState("");
    const [ results, setResults ] = useState([]);

    console.log(results)

    useEffect(() => {
        // METHOD ONE of using async/await in useEffect (HELPER FUNCTION)
        // const search = async () => {
        //     await axios.get('wikipedia api')
        // }
        // search()

        // METHOD TWO: Define function and automatically call it with () following it. (HELPER FUNCTION)
        // (async () => {
        //     await axios.get('wikipedia api')
        // })();

        // METHOD THREE: Make use of promises
        // axios.get('wikipedia api').then((res) => {
        //     console.log(res.data);
        // });
        //en.wikipedia.org/w/api.php?action=query&list=search&format=json&origin=*&srsearch=programming

        const search = async () => {
            const { data } = await axios.get('https://en.wikipedia.org/w/api.php', {
                params: {
                    action: 'query',
                    list: 'search',
                    origin: '*',
                    format: 'json',
                    srsearch: term,
                },
            });
            setResults(data.query.search);
        }

        if (term && !results.length) {
            search()
        } else {

            const timeoutId = setTimeout(() => {
                if (term) {
                    search()
                }
            }, 800);

            return () => {
                clearTimeout(timeoutId)
            };
        }

    }, [ term, results.length ]);

    const renderedResults = results.map((result) => {
        return <div key={result.pageid} className="item">
            <div className="right floated content">
                <a href={`https://en.wikipedia.org?curid=${ result.pageid }`} className="ui button">Go</a>
            </div>
            <div className="content">
                <div className="header">
                    {result.title}
                </div>
                <span dangerouslySetInnerHTML={{ __html: result.snippet }}></span>
            </div>
        </div>
    })

    return (
        <div>
            <div className="ui form">
                <div className="field">
                    <label>Enter Search Term</label>
                    <input
                        className="input"
                        value={term}
                        onChange={e => setTerm(e.target.value)}
                    />
                </div>
            </div>
            <div className="ui celled list">
                {renderedResults}
            </div>
        </div>
    );
}

export default Search;

