import React from 'react';

const Link = ({ className, href, children }) => {
    const onClick = (e) => {
        if (e.metaKey || e.ctrlKey) {
            return;
        }

        //to ensure we don't do a full page reload on start
        e.preventDefault();
        window.history.pushState({}, '', href);

        //weird code that will tell app that route/url changed
        const nEvent = new PopStateEvent('popstate');
        window.dispatchEvent(nEvent);
    };

    return (
        <a onClick={onClick} className={className} href={href} >
            {children}
        </a>
    )
};

export default Link;