import React, { useState, useEffect, useRef } from 'react';
import DropDown from './DropDown';
import Convert from './Convert';

const options = [
    {
        label: 'Afrikaans',
        value: 'af'
    },
    {
        label: 'Arabic',
        value: 'ar'
    },
    {
        label: 'Hindi',
        value: 'hi'
    },
    {
        label: 'Armenian',
        value: 'hy'
    },
    {
        label: 'Bengali',
        value: 'bn'
    },
    {
        label: 'Chinese (Simplified)',
        value: 'zh'
    },
    {
        label: 'French',
        value: 'fr'
    },
    {
        label: 'German',
        value: 'de'
    },
    {
        label: 'Greek',
        value: 'el'
    },
    {
        label: 'Hebrew',
        value: 'he'
    },
    {
        label: 'Irish',
        value: 'ga'
    },
    {
        label: 'Italian',
        value: 'it'
    },
    {
        label: 'Japanese',
        value: 'ja'
    },
    {
        label: 'Korean',
        value: 'ko'
    },
    {
        label: 'Russian',
        value: 'ru'
    },
    {
        label: 'Spanish',
        value: 'es'
    },
    {
        label: 'Uzbek',
        value: 'uz'
    },
]

const Translate = () => {
    const [ language, setLanguage ] = useState(options[ 0 ])
    const [ text, setText ] = useState('')

    return (
        <div>
            <div className='ui form'>
                <div className="field">
                    <label>Enter text</label>
                    <input value={text} onChange={(e) => { setText(e.target.value) }} />
                </div>
            </div>
            <DropDown
                label='Select a language'
                options={options}
                selected={language}
                setSelected={setLanguage}
            />
            <hr />
            <h3 className="ui header">Output</h3>
            <Convert text={text} language={language} />
        </div>
    )
}

export default Translate;